<template>
    <button class="inline-flex items-center px-4 py-2 border border-gray-300 rounded-md font-semibold text-xs text-gray-700 uppercase tracking-widest shadow-sm hover:text-gray-500 focus:outline-none focus:border-blue-300 focus:shadow-outline-blue active:text-gray-800 active:bg-gray-50 transition ease-in-out duration-150" :class="bgClass">
        <slot></slot>
    </button>
</template>

<script>
    export default {
        props: {
            type: {
                type: String,
                default: 'success',
            },
        },
        computed:{
            bgClass () {
                return this.type === 'success' ? 'bg-green-500' : 'bg-yellow-600'
            }
        },
    }
</script>
